  .main-nav {
    background-color: #0000;
    margin-left: 0;
    display: flex;
    position: relative;
  }

  .nav-links {
    width: 100%;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    margin-top: 0;
  }

  .link-item {
